


































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			adressinf:{
				"address": "",
				"cityCode": "",
				"cityName": "",
				"countyAreaCode": "",
				"countyAreaName": "",
				"defaultType": 0,
				"name": "",
				"phone": "",
				"provinceCode": "",
				"provinceName": ""
			},
			id:''
		}
	},
	onLoad(opt) {
		if(opt._id){
			this.id=opt._id;
			this.req_addressInf(opt._id);
		}
	},
	methods: {
		bindRegionChange: function (e) {
			if(e.target.value[2]!="通州区"){
				ut.totast('现在只开通通州的服务，其他区域敬请期待！');
				return
			}
		 	this.adressinf.provinceName=e.target.value[0];
		 	this.adressinf.countyAreaName=e.target.value[1];
		 	this.adressinf.cityName=e.target.value[2];
		},
		cg_active(){
			if(this.adressinf.defaultType){
				this.adressinf.defaultType=0;
				return;
			}
			this.adressinf.defaultType=1;
		},
		req_addaddress(){
			let url="address/add";
			if(this.id){
				url="address/update"
			}
			if(!this.adressinf.name){
				ut.totast('请输入姓名');
				return;
			}
			if(!ut.checkmobile(this.adressinf.phone)){
				ut.totast('请输入正确的手机号');
				return;
			}
			if(!this.adressinf.provinceName){
				ut.totast('请选择所在城市');
				return;
			}
			if(!this.adressinf.address){
				ut.totast('请输入详细地址');
				return;
			}
			ut.request({
				data:this.adressinf,
				url: url,
				c:true
			}).then(data=>{
				wx.navigateBack()
			})
		},
		req_addressInf(id){
			ut.request({
				data:{
				id: id
				},
				url: "address/detail"
			}).then(data => {
				this.adressinf=data;
			})
		}
	}
}
